import React from 'react'
import { Link } from 'react-router-dom'

const New = (props) => {
    const re = /[\s,.´"]+/igm;
    const title = `${props.article.title.slice(0, 15).replace(re, "-")}`;
    return (
        <div className="new">
                <img src={props.article.urlToImage} alt={props.article.autor}></img>
            <Link to={`/${props.search}/${title}`}>
                <h4>{props.article.title}</h4>
            </Link>
                <span className="new-date"> <i className="fa fa-clock-o"></i> {props.article.publishedAt.slice(0, 10)}</span>
                <div className="new-description">
                    <p>{props.article.description}</p>
                </div>
        </div>
    )
}
export default New