import React, { useEffect, useState } from 'react'

const Exchanges = () => {
    const [exchanges, setExchanges] = useState({});

    useEffect(() => {
        const getExchanges = async () => {
            const resp = await fetch('https://api.exchangeratesapi.io/latest');
            const data = await resp.json();
            setExchanges(data);
        }
        getExchanges();
    }, []);
    if ('rates' in exchanges) {
        return (
            <ul className="exchanges-list">
                <li>Base: EUR</li>
                {Object.entries(exchanges.rates).slice(0, 10).map((e,i) => {
                    return (
                        <li key={i}>{e[0]} : {e[1].toFixed(2)}</li>
                    )
                }
                )}
            </ul>
        )
    } else {
        return (
            <p>Error</p>
        )
    }

}
export default Exchanges
