import React, { useEffect, useState } from 'react'
import News from './news'
import { Route } from 'react-router-dom'
import NewPage from "./newPage"

const Home = (props) => {
    const [news, setNews] = useState([]);
    const [newsPage, setnewsPage] = useState([]);

    const API_KEY = "dbde088d8ee74c08913a18dab2f74cc0";
    function getDate() {
        let today = new Date();
        let dd = String(today.getDate()).padStart(2, '0');
        let mm = String(today.getMonth() + 1).padStart(2, '0'); //January is 0!
        let yyyy = today.getFullYear();
        today = yyyy + '-' + mm + '-' + dd;
        return today;
    }

    useEffect(() => {
        console.log("ASD");
        const date = getDate();
        const getNews = async () => {
            var url = 'https://newsapi.org/v2/everything?' +
                `q=${props.search}&` +
                `from=${date}&` +
                'sortBy=popularity&' +
                `apiKey=${API_KEY}`;
            const resp = await fetch(url);
            const data = await resp.json();
            setNews(data.articles);
        }
        getNews();
    }, [props.search]);

    useEffect(() => {
        const re = /[\s,.´"]+/igm;
        let pages = news.map((e, i) => {
            const title = `${e.title.slice(0, 15).replace(re, "-")}`;
            return (
                <Route key={title+i} path={`/${props.search}/${title}`} render={() => <NewPage key={title+i} article={e} />} />
            );
        });
        setnewsPage(pages);
    }, [news,props.search]);

    return (
        <React.Fragment>
            <Route exact path={`/${props.search}`} render={() => <News news={news} search={props.search} />} />
            <Route exact path={`/news`} render={() => <News news={news} search={props.search} />} />
            <Route exact path="/kNews-react" render={() => <News news={news} search={props.search} />} />
            {newsPage}
        </React.Fragment>
    );
}

export default Home
