import React from 'react'
import New from "./new"
import MainNew from "./MainNew"

const News = (props) => {    
    return (
        <div className="main-container">
            <div className="container">
                {props.news.slice(0, 1).map((article, i) => (
                    <MainNew
                        key={i}
                        search={props.search}
                        article={article}
                    ></MainNew>
                ))}
                <div className="news">
                    {props.news.slice(1, 10).map((article, i) => (
                        <New
                            key={i}
                            search={props.search}
                            article={article}
                        ></New>
                    ))}
                </div>
            </div>
        </div>
    )
}
export default News
