import React, { useState } from 'react'
import { Navbar, Nav, Form, FormControl, Button } from 'react-bootstrap'
import { BrowserRouter, Route, Link } from 'react-router-dom'
// import styles from './css/nav_menu.module.css';
import Home from "./components/home"
import Exchanges from './components/exchanges'

function App() {
  const [search, setSearch] = useState("new");

  const submitQuery = e => {
    e.preventDefault();
    setSearch(e.target[0].value);
  }

  return (
    <BrowserRouter>
      <div className="App">
        <Navbar collapseOnSelect expand="lg" bg="dark" variant="dark">
          <Link to="/news" className="navbar-brand">Home</Link>
          <Navbar.Toggle aria-controls="responsive-navbar-nav" />
          <Navbar.Collapse id="responsive-navbar-nav">
            <Nav className="links">
              <Link to="/World" className="nav-link">World</Link>
              <Link to="/Politics" className="nav-link">Politics</Link>
              <Link to="/Business" className="nav-link">Business</Link>
              <Link to="/Health" className="nav-link">Health</Link>
              <Link to="/Entertainment" className="nav-link">Entertainment</Link>
              <Link to="/Style" className="nav-link">Style</Link>
              <Link to="/Travel" className="nav-link">Travel</Link>
              <Link to="/Sport" className="nav-link">Sport</Link>
            </Nav>
          </Navbar.Collapse>
          <Form inline onSubmit={submitQuery}>
            <FormControl type="text" placeholder="Search" className="mr-sm-2" />
            <Button variant="outline-info">Search</Button>
          </Form>
        </Navbar>
        <Exchanges></Exchanges>
      </div>
      <Route exact path="/kNews-react" render={() => <Home search={search} />} />
      <Route path="/news" render={() => <Home search={search} />} />
      <Route path={`/${search}`} render={() => <Home search={search} />} />
      <Route path="/World" render={() => <Home search={"World"} />} />
      <Route path="/Politics" render={() => <Home search={"Politics"} />} />
      <Route path="/Business" render={() => <Home search={"Business"} />} />
      <Route path="/Health" render={() => <Home search={"Health"} />} />
      <Route path="/Entertainment" render={() => <Home search={"Entertainment"} />} />
      <Route path="/Style" render={() => <Home search={"Style"} />} />
      <Route path="/Travel" render={() => <Home search={"Travel"} />} />
      <Route path="/Sport" render={() => <Home search={"Sport"} />} />
      <footer>
        <p>Posted by: K</p>
        <p>Contact information: <a href="mailto:someone@example.com">
          someone@example.com</a>.</p>
      </footer>
    </BrowserRouter>

  );
}

export default App;
